import Vue from 'vue'
import Router from "vue-router"
import Home from '@/components/Home/Home.vue'
import Category from '@/components/Category/Category.vue'
import CategoryPhone from '@/components/Category/components/Phone.vue'
import CategoryLaptop from '@/components/Category/components/Laptop.vue'
import CategoryTablet from '@/components/Category/components/Tablet.vue'
import CategoryGamingLaptop from '@/components/Category/components/GamingLaptop.vue'
import CategoryComputerAccessories from '@/components/Category/components/ComputerAccessories.vue'
import Hot from '@/components/Hot/Hot.vue'
import New from '@/components/New/New.vue'
import Product from '@/components/Product/Product.vue'
import ShoppingCart from '@/components/ShoppingCart/ShoppingCart.vue'
import ERROR from '@/components/Notfound.vue'

Vue.use(Router)

export default new Router({
	routes: [{
		path: "*",
		component: ERROR,
		meta: {
			title: "404 not found"
		}
	}, {
		path: "/",
		redirect: "/Home",
	}, {
		path: "/Home",
		name: 'Home',
		component: Home,
		meta: {
			title: "Home"
		}
	}, {
		path: "/Category",
		component: Category,
		children: [{
			path: "",
			redirect: "Phone"
		}, {
			path: "Phone",
			name: 'Phone',
			component: CategoryPhone,
			meta: {
				title: "Phone"
			}
		}, {
			path: "Phone/:category",
			component: CategoryPhone,
			meta: {
				title: "Phone"
			}
		}, {
			path: "Laptop",
			name: 'Laptop',
			component: CategoryLaptop,
			meta: {
				title: "Laptop"
			}
		}, {
			path: "Tablet",
			name: 'Tablet',
			component: CategoryTablet,
			meta: {
				title: "Tablet"
			}
		}, {
			path: "GamingLaptop",
			name: 'GamingLaptop',
			component: CategoryGamingLaptop,
			meta: {
				title: "Gaming Laptop"
			}
		}, {
			path: "ComputerAccessories",
			name: 'ComputerAccessories',
			component: CategoryComputerAccessories,
			meta: {
				title: "Computer Accessories"
			}
		}]
	}, {
		path: "/Hot",
		name: 'Hot',
		component: Hot,
		meta: {
			title: "Hot"
		}
	}, {
		path: "/New",
		name: 'New',
		component: New,
		meta: {
			title: "New"
		}
	}, {
		path: "/Product/:id",
		name: 'Product',
		component: Product,
		meta: {
			title: "Product"
		}
	}, {
		path: "/ShoppingCart",
		name: 'ShoppingCart',
		component: ShoppingCart,
		meta: {
			title: "ShoppingCart"
		}
	}],
	mode: 'history'
})



