<template>
	<div id="shopping-cart">
		<div class="cart-title">
			<h1>Shopping Cart</h1>
		</div>
		<div class="cart-items">
			<div class="cart-item" v-for="item in this.cartList">
				<div class="item-img">
					<img src="../../../static/imgs/iphone11white.png" />
				</div>
				<div class="item-desc">
					<div class="item-title">{{item.id}}</div>
					<div class="item-spec">{{item.color}}</div>
					<div class="item-spec">{{item.capacity}}</div>
				</div>
				<div class="item-amount">
					<div class="counter">
						<div class="counter-decrease trans03" @click="decreaseAmount(item.amount)">
							<font-awesome-icon :icon='["fas", "minus"]'></font-awesome-icon>
						</div>
						<input class="counter-number" type="number" v-model="item.amount">
						<div class="counter-increase trans03" @click="increaseAmount(item.amount)">
							<font-awesome-icon :icon='["fas", "plus"]'></font-awesome-icon>
						</div>
					</div>
				</div>
				<div class="item-price">
					<p>10999￥</p>
					<div class="remove-item">Remove</div>
				</div>
			</div>
		</div>
		<div class="submit">
			<div class="price-total">Total:10999</div>
			<div class="submition">
				<div class="checkout">Proceed to checkout</div>
				<div class="paypel">Paypal</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "shopping-cart",
		data() {
			return {
				cartList: [{
					"id": "11001",
					"color": "green",
					"capacity": "128",
					"amount": "1"
				}],
				amount: 1
			}
		},
		methods: {
			decreaseAmount(mount) {
				if(mount == 1) {
					return
				}
				mount --
			},
			increaseAmount(mount) {
				mount ++
			},
			removeItem() {
				
			}
		},
		computed: {
			
		}
	}
</script>

<style scoped>
	@import url("../../assets/roots.css");
	#shopping-cart {
		box-sizing: border-box;
		width: 68.75rem;
		margin: 5rem auto;
	}
	
	.cart-title {
		box-sizing: border-box;
		border-bottom: 1px solid var(--color-dark);
	}
	
	.cart-items {
		padding: 1rem 0;
		border-bottom: 1px solid var(--color-dark);
	}
	
	.cart-item {
		display: flex;
		align-items: flex-start;
		padding: 1rem 0;
	}
		
	.item-img {
		user-select: none;
		height: 12.5rem;
		width: 12.5rem;
		padding: 0.8rem;
		box-sizing:border-box;
		border: 1px solid var(--color-gray);
		border-radius: 8px;
	}
	
	.item-img img {
		height: 100%;
		width: 100%;
	}
	
	.item-desc {
		width: 27.85rem;
		margin: 0 2rem;
		padding-top: 1rem;
	}
	
	.item-title {
		color: var(--theme-main);
		font-size: 32px;
		font-weight: bolder;
		margin-bottom: 1rem;
	}
	
	.item-spec {
		color: var(--theme-sub);
		font-size: 22px;
		margin-bottom: 0.5rem;
	}
	
	.item-amount {
		width: 10.25rem;
	}
	
	.counter {
		display: flex;
		margin-top: 1rem;
		box-sizing: border-box;
		border: 1px solid var(--color-dark);
	}
	
	.counter-decrease,
	.counter-increase {
		box-sizing: border-box;
		width: 33.3%;
		padding: 0.8rem;
		text-align: center;
		cursor: pointer;
	}
	
	.counter-decrease:hover,
	.counter-increase:hover {
		color: var(--color-light);
		background: var(--color-dark);
	}
	
	.counter-number {
		width: 33.3%;
		text-align: center;
		outline: none;
		border: none;
		border-left: 1px solid var(--color-dark);
		border-right: 1px solid var(--color-dark);
		font-size: 22px;
	}
	
	.counter-number::-webkit-outer-spin-button,
	.counter-number::-webkit-inner-spin-button {
		-webkit-appearance: none;
	}
	
	.item-price {
		display: flex;
		align-items: flex-end;
		flex-direction: column;
		justify-content: space-between;
		width: 14.15rem;
	}
	
	.item-price p {
		margin-top: 1rem;
		margin-bottom: 0;
		color: var(--color-red);
		font-size: 38px;
		font-family: impact;
		text-align: center;
	}
	
	.remove-item {
		text-decoration: underline;
		font-size: 18px;
		margin-top: 7rem;
		margin-right: 1rem;
		cursor: pointer;
	}
	
	.submit {
		padding-top: 2rem;
	}
</style>



